.servises_list {
    a{
        color: var(--color-blue);
        &:hover {
            color: var(--color-yellow)!important;
        }
        &:last-child {
            .servises_item{
                border-bottom: none;
            }
        }
    }

    .servises_item {
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ccc;
        margin-left: 0px!important;
        padding-bottom: 20px;
        padding-top: 20px;
        width: 100%;
        &_img {
            height: 268px;
            max-width: 400px;
            padding: 0px!important;
            box-shadow: 3px 3px 3px rgba(25, 25, 30, 0.3);
            img {
                width: 100%;
                height: 100%;
                cursor: pointer;
            }
            &:hover {
                background: #fff;
                opacity: 0.8;
            }
        }
    }
}

@media screen and (max-width: 1400px){
    .servises_item{ 
        &_img {
            height: 230px !important;
        }
    }
}

@media screen and (max-width: 1200px){
    .servises_item{
        &_img {
            height: 210px !important;
        }
    }
}

@media screen and (max-width: 992px){
    .servises_item{
        justify-content: center;
        &_img {
            margin: 0px auto;
            height: 263px !important;
        }
        &_title {
            margin-top: 20px;
            text-align: center;
        }
    }
}

@media screen and (max-width: 434px){
    .servises_item{
        justify-content: center;
        &_img {
            margin: 0px auto;
            height: 215px !important;
        }
        &_title {
            margin-top: 20px;
            text-align: center;
        }
    }
}